.weigh-address {
  margin-top: 4%;
}
.weigh-address .address-form-button {
  margin-left: 5%;
  width: 95%;
  height: 44px;
}
.weigh-address .weigh-address-input {
  width: 100%;
  height: 44px;
}
.weigh-address .weigh-card-left {
  width: 100%;
  height: 100%;
}
.weigh-address .weigh-card-left p {
  font-size: 14px;
  margin-top: 12%;
}
.weigh-address .weigh-card-left .copy-max {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.06);
  overflow: hidden;
  height: 48px;
  border-radius: 4px;
  line-height: 48px;
  font-size: 14px;
  background: #f9f9f9;
}
.weigh-address .weigh-card-left .copy-max .address {
  display: inline-block;
  margin-left: 17px;
  overflow: hidden;
  /*超出部分隐藏*/
  white-space: nowrap;
  /*让文本强制在一行不换行*/
  text-overflow: ellipsis;
  /*显示省略号来代表被修剪的文本*/
}
.weigh-address .weigh-card-left .copy-max .copy {
  color: #0091FF;
  float: right;
  padding: 0 28px;
  cursor: pointer;
}
.weigh-address .weigh-card-right {
  width: 100%;
  height: 100%;
}
.weigh-address .address-crad-button {
  float: right;
  margin-left: 2%;
  width: 15%;
  margin-top: 4%;
}
.weigh-state .weigh-box {
  width: 100%;
  height: 50px;
  line-height: 50px;
  margin-bottom: 2%;
  padding-left: 4%;
  border: 1px solid rgba(0, 0, 0, 0.06);
  border-radius: 4px;
}
@media only screen and (max-width: 768px) {
  .weigh-address .weigh-card-left p {
    margin-top: 0;
  }
  .weigh-address .weigh-card-right {
    margin-top: 6%;
  }
  .weigh-address .address-crad-button {
    float: right;
    margin-left: 2%;
    width: 40%;
  }
  .weigh-state .weigh-box {
    width: 100%;
    height: 50px;
    line-height: 50px;
    margin-bottom: 2%;
    padding-left: 4%;
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 4px;
    overflow-x: auto;
    overflow-y: hidden;
  }
}
