.share-titles {
  margin-bottom: 32px;
  color: #000;
}
.share-title {
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
  margin-bottom: 0px;
}
.explain {
  float: right;
  line-height: 30px;
  cursor: pointer;
}
