  margin-bottom: 0px;
}
@media only screen and (max-width: 768px) {
  .share-titles {
    margin-bottom: 32px;
  }
  .share-title {
    font-size: 20px;
    font-weight: bold;
    display: inline-block;
    margin-bottom: 0px;
  }
}
